import React from "react";
import TextPrimary from "components/Text/TextPrimary";
const AgentPromptMessage = ({ rate , newValue = 50}) => {
    return (
        <div>
            {newValue}%<TextPrimary
            >
                {rate * newValue / 100}%
            </TextPrimary>{" "}
            ({rate}% X {newValue}%)
        </div>
    );
};

export default AgentPromptMessage;



